import React from 'react'
import './Navbar.css'
class Navbar extends React.Component {
  listener = null;
  state = {
    nav:false
  }
  async componentDidMount() {
     window.addEventListener("scroll", this.handleScroll);
   }
//    async componentWillUnmount() {
//       window.removeEventListener('scroll', this.handleScroll);
//     }
   handleScroll= () => {
     if (document.getElementsByClassName('faixa-ano')[0].offsetTop > 140) {
         if(!this.state.nav){
           this.setState({ nav: true });
         }
     }else{
         if(this.state.nav){
           this.setState({ nav: false });
         }
     }
     console.log('sdsfdsfsd')
   }

  render(){
  return (
    <div>
    <div className={this.state.nav ? 'nav' : 'Nav__black'}>
        <header className="faixa-ano" id="faixa-ano">
          <h4>Filtre por ano de distribuição do processo{' '}
            <select className="ano" id="filtro-ano">
              <option value="">--Todos--</option>
              <option value="2007">2007</option>
              <option value="2008">2008</option>
              <option value="2009">2009</option>
              <option value="2010">2010</option>
              <option value="2011">2011</option>
              <option value="2012">2012</option>
              <option value="2013">2013</option>
              <option value="2014">2014</option>
              <option value="2015">2015</option>
              <option value="2016">2016</option>
              <option value="2017">2017</option>
              <option value="2018">2018</option>
              <option value="2019">2019</option>
              <option value="2020">2020</option>
              <option value="2021">2021</option>
            </select>
          </h4>
        </header>
    </div>
    </div>
  );}
}
export default Navbar

